import { ChangeDetectionStrategy, Component, Input, OnInit } from '@angular/core';
import { combineLatest, Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { RxActionButtonService } from '../action-button.service';
import { ActionButtonDesignModel } from './action-button-design.model';
import { ActionButtonSize } from '../action-button.types';

@Component({
  selector: 'rx-action-button-design',
  templateUrl: './action-button-design.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ActionButtonDesignComponent implements OnInit {
  @Input()
  model: ActionButtonDesignModel;

  iconCssClass$: Observable<string>;
  // ButtonColorType must be used instead of any but it's not exported by ADAPT.
  buttonType$: Observable<any>;
  size$: Observable<ActionButtonSize>;

  constructor(private rxActionButtonService: RxActionButtonService) {}

  ngOnInit(): void {
    this.iconCssClass$ = combineLatest([this.model.icon$, this.model.iconAlignment$]).pipe(
      map(([iconCssName, iconAlignment]) => this.rxActionButtonService.getIconCssClass(iconCssName, iconAlignment))
    );

    this.buttonType$ = this.model.style$.pipe(map((style) => this.rxActionButtonService.getButtonType(style)));
    this.size$ = this.model.size$.pipe(map((size) => size || ActionButtonSize.Default));
  }
}
